@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap");
@import url("http://fonts.cdnfonts.com/css/menlo");

* {
  font-family: "DM Sans", sans-serif;
}

.skills-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  min-height: fit-content;
  margin: 120px 0;
}

.skills-card {
  display: flex;
  height: 360px;
  width: 1000px;
  /* margin: 80px 0; */
}

.skills-bullets {
  box-shadow: 15px 0 9px -15px #1f2235;
  height: 100%;
  width: 320px;
}

.skills-bullet-details {
  flex-grow: 1;
  width: 600px;
  overflow: hidden;
  padding: 0 0 0 80px;
}

.experience-description {
  margin: 10px 0 0 0;
  text-align: justify;
}

.skills-title {
  font-family: "DM Sans", sans-serif;
}

.skills-description-text {
  font-size: 12px;
}

.skills-details-carousel {
  transition: transform 1s ease-out;
}

.bullet-container {
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  position: relative;
}

.bullets {
  width: 86%;
  position: relative;
  z-index: 2;
}

.bullet {
  display: flex;
  align-items: center;
  cursor: pointer;
  background-color: var(--secondary-color);
  height: 30px;
  width: 30px;
  margin: 15px 0;
  padding: 0 8px;
  border-radius: 20px;
  transition: width 0.6s ease;
}

.bullet-label {
  font-size: 14px;
  white-space: nowrap;
  font-weight: 500;
}

.bullet-logo {
  height: 16px;
  margin: 0 30px 0 0;
}

.selected-bullet {
  color: var(--white);
  background-color: var(--gray-color);
  width: 100%;
}

.skills-screen-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 360px;
  width: 100%;
}

.skills-heading {
  display: flex;
  flex-direction: column;
}

.skills-main-heading {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.skills-main-heading span {
  color: var(--primary-color);
  font-size: 16px;
}

.experience-description {
  max-width: 100%;
}

.heading-bullet {
  position: absolute;
  left: -30px;
  height: 15px;
  width: 15px;
  top: 5px;
  border-radius: 50%;
}

.skills-heading-description {
  font-size: 12px;
  text-align: justify;
}

.skill-parent {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 50%;
}

.programming-skills-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.skill-percentage {
  position: relative;
  height: 15px;
  width: 70%;
  background-color: var(--secondary-color);
}

.skill-percentage .active-percentage-bar {
  height: 15px;
  transition: 0.8s ease;
  position: absolute;
  background-color: var(--gray-color);
}

.skill-parent span {
  font-size: 16px;
  color: var(--primary-color);
}

/* IPAD PRO RESPONSIVENESS */
@media only screen and (max-width: 1110px) {
  .skills-card {
    width: 880px;
  }

  .skills-bullet-details {
    padding: 0 0 0 70px;
  }
}

/* IPAD RESPONSIVENESS */
@media only screen and (max-width: 1023px) {
  .skills-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .bullets {
    width: 90%;
  }

  .skills-card {
    height: fit-content;
    display: flex;
    flex-direction: column;
    margin: 30px 0;
    align-items: center;
    width: 90%;
  }

  .skills-bullet-details {
    height: 360px;
    padding: 0;
    width: fit-content;
  }

  .skills-bullets {
    margin: 30px 0;
    width: 100%;
  }

  .selected-bullet {
    width: 100%;
  }
}
